<template>
  <el-row :gutter="20" type="flex" justify="center">
    <el-col :span="15">
      <div class="grid-content">
        <!-- 标题 -->
        <el-row :gutter="20" type="flex" justify="center">
          <el-col :span="15">
            <div class="grid-content title">
              <h1>登录</h1>
            </div>
          </el-col>
        </el-row>
        <!-- 表单 -->
        <el-row :gutter="20" type="flex" justify="center">
          <el-col :span="15">
            <div class="grid-content">
              <el-form
                :model="ruleForm"
                ref="ruleForm"
                label-width="100px"
                class="demo-ruleForm"
                label-position="left"
                :rules="rules"
              >
                <el-form-item label="用户名" prop="user">
                  <el-input type="text" v-model="ruleForm.user"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="psw">
                  <el-input type="password" v-model="ruleForm.psw"></el-input>
                </el-form-item>
                 
                <el-form-item >
                  <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                  <el-button @click="resetForm('ruleForm')">重置</el-button>
                </el-form-item>
              </el-form>
              <div class="span">
                <span>忘记密码?</span>
                <span>|</span>
                <span>
                  <router-link to="/user/register" style="text-decoration:none;color:blue">快速注册</router-link>
                </span>
              </div>
            </div>
          </el-col>
        </el-row>
        <!-- 表单结束 -->
      </div>
    </el-col>
  </el-row>
</template>

<script>
export default {
  data() {
     
    return {
      ruleForm: {
        user: "",
        psw: "",
       
      },
      rules: {
        user: [{ required: true, message: "请输入用户名", trigger: "blur" }],
        psw: [{ required: true, message: "请输入密码", trigger: "blur" }],
      

      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$store.dispatch("getUsername",this.ruleForm.user)
          this.$router.push({path:"/main/index/news"})
          console.log(this.ruleForm.user);
          
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style scoped>
.el-row {
  margin-bottom: 20px;
}
.el-col {
  border-radius: 4px;
}
.bg-purple {
  background: #d3dce6;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.title {
  /* border: solid; */
  text-align: center;
}

.el-button {
  margin-left: 100px;
}

.span {
  width: 40%;
  margin: auto;
  /* border: solid 1px; */
  display: flex;
  justify-content: space-around;
}
.span span:first-child {
  color: gray;
}
</style>


